import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Registers from '@/components/Registers'
Vue.use(Router)

export default new Router({
  routes: [{
    path: '/',
    name: 'Registers',
    component: Registers
  }]
})



