<template>
  <div class="container">
    <br>
  <h2>{{title}}</h2>
  <h3 v-if="userId !=''">User_id : {{userId}}</h3>
  <h3 v-if="tel !=''">เบอร์โทร : {{tel}}</h3>
  <form>
  <div class="form-group">
    <input type="tel" class="form-control"  placeholder="เบอร์โทร..." maxlength="10" v-model="tel">
  </div>
  <button type="submit" class="btn btn-success">ลงทะเบียน</button>
</form>
  </div>
</template>
<script>
export default {
  name: "Registers",
  data() {
    return {
      title: "ลงทะเบียน",
      name: [],
      userId: "",
      profile: [],
      send: false,
      tel: ""
    };
  },
  created() {
    let vm = this;
    window.onload = function(e) {
      liff.init(function(data) {
        vm.initializeApp(data);
      });
    };
    console.log("ok");
  },
  methods: () => {
    initializeApp = data => {
      let vm = this;
      //vm.name.push(data);
      vm.userId = data.context.userId;
      console.log(data);
    };
  }
};
</script>





